/*----*/

/*Общие стили*/

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  min-width: 300px;
  background: #fff;
  position: relative;
}

.wrapper {
  max-width: 1230px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.wrap {
  max-width: 1170px;
  margin-left: auto;
  margin-right: auto;
  font-size: 0;
}

.ears::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  cursor: pointer;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

input {
  outline: none;
}

.d-none {
  display: none;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

/*--ТВИМ--*/

/*Общее для сайта*/

.header.active {
  display: block;
  opacity: 1;
  z-index: 10000000;
}

.headerBlock__img {
  display: block;
}

/*-------------------------------*/

@keyframes shake {
  from, to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  10%, 30%, 50%, 70%, 90% {
    -webkit-transform: translate3d(-10px, 0, 0);
    transform: translate3d(-10px, 0, 0);
  }

  20%, 40%, 60%, 80% {
    -webkit-transform: translate3d(10px, 0, 0);
    transform: translate3d(10px, 0, 0);
  }
}

