.inputItem__inp:focus + .inputItem__label,
.inputItem__inp:not(:focus):valid + .inputItem__label {
  top: -33px;
  font-size: 16px;
}

/*-------------------------------*/

/*-------------------------------*/

/*Стилизация чекбокса*/

.checkbox {
  display: none;
}

.checkbox-custom {
  position: relative;
  width: 16px;
  height: 16px;
  border: 1px solid #fff;
  border-radius: 3px;
}

.checkbox-custom,
.label {
  display: inline-block;
  vertical-align: middle;
}

.checkbox:checked + .checkbox-custom::before {
  content: "";
  display: block;
  position: absolute;
  top: 2px;
  right: 2px;
  bottom: 2px;
  left: 2px;
  background: #fff;
  border-radius: 2px;
}

