/*-------------------------------*/

/*-------------------------------*/

.overlay {
  position: absolute;
  z-index: 10000000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: none;
  opacity: 0;
}

.hideForm {
  display: none;
  opacity: 0;
}

